import {FBFieldWithPriceBaseOptions} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";
import {FBFieldWithPriceBaseModel} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Model";
import {FBColumnModel} from "FormBuilder/Core/FBColumn.Model";
import {FBHiddenOptions} from "./FBHidden.Options";


export class FBHiddenModel extends FBFieldWithPriceBaseModel<FBHiddenOptions,FBColumnModel>{


    public Value:string;

    GetValue() {
        if(!this.GetIsVisible())
            return '';
        return this.Value;
    }


    GetDynamicFieldNames(): string[] {
        return ['FBHidden'];
    }

    GetStoresInformation(): boolean {
        return true;
    }

    InitializeStartingValues() {
    }

}

