import * as React from "react";
import {FBFieldWithPriceBase, FBFieldWithPriceBaseState} from "FormBuilder/Fields/Core/FBFieldWithPriceBase";
import {OptionsCalculator} from "FormBuilder/Core/Calulators/OptionsCalculator";
import {PriceTypeEnum} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {FBFieldBase, FBFieldBaseProps, FBFieldBaseState} from "FormBuilder/Fields/Core/FBFieldBase";
import {FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";
import {FBHiddenOptions} from "./FBHidden.Options";
import {GetFieldOptions} from "FormBuilder/Events/GetFieldOptions";
import {GetField} from "FormBuilder/Events/GetField";
import {GetModel} from "FormBuilder/Events/GetModel";
import {FBRepeaterModel} from "../FBRepeater/FBRepeater.Model";
import {FBRepeaterOptions} from "../FBRepeater/FBRepeater.Options";
import {FBHiddenModel} from "./FBHidden.Model";
import {EventManager} from "shared/core/Events/EventManager";


export class FBHidden extends FBFieldBase<FBDropDownProps,FBDropDownState>{

    static defaultProps: Partial<FBDropDownProps> = {

    };

    GetStoresInformation(): boolean {
        return true;
    }


    public SubRender() {
        if(this.Column.IsDesign)
            return(
                <div className={'rnHidden'}  style={{opacity:.5}}>
                    <div className={'rednaoLabel'}>
                        <label style={{fontWeight:'bold'}}>Hidden</label>
                    </div>
                    <input readOnly={true} className={'rnInputPrice'}  style={{width:'100%'}} type={'text'} value={this.props.Model.Value} onChange={x=>{this.props.Model.Value=x.target.value;this.props.Model.Refresh();}} />
                </div>
            )
        else
            return <input type={'hidden'} value={this.props.Model.Value}/>
    }



}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface FBDropDownProps extends FBFieldBaseProps{
    Model:FBHiddenModel;

}

interface FBDropDownState extends FBFieldBaseState{

}


EventManager.Subscribe(GetModel,(event)=>{
    if(event.Options.Type==FieldTypeEnum.Hidden)
        return new FBHiddenModel(event.Options as FBHiddenOptions,event.parent).Initialize(event.PreviousData);
});


EventManager.Subscribe(GetFieldOptions,(event)=>{
    if(event.Type==FieldTypeEnum.Hidden)
        return new FBHiddenOptions();
});

EventManager.Subscribe(GetField,(event)=>{
    if(event.Model.Options.Type==FieldTypeEnum.Hidden)
        return <FBHidden Model={event.Model as FBHiddenModel} />
});
//#endregion "My Region"